var token=""
var allAmonut=0
export default {
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			addressData: {},
			goodsData:[],
			price:0,
			number:1,
		}
	},
	onLoad(option){
		// 商品数据
		// let data = JSON.parse(option.data);
		// console.log(data);
		const map = new Map()
		map.set(1,"aaa")
		this.goodsData = JSON.parse(decodeURIComponent(option.orders));
		if(option.price>0)
		{//判断是否购物车跳转而来
			this.price = option.price;
			allAmonut = this.price
		}else
		{
			allAmonut = this.goodsData[0].price
		}
		console.log(this.goodsData)
		console.log(this.price)
		console.log(allAmonut)
		console.log(map)
		token = uni.getStorageSync("token")
		this.loadadr()
	},
	methods: {
		loadadr()
		{	
			let _this = this;
			uni.request({
			    url:this.$url+'/user/address/all' ,//服务器端地址
			    data: {
					
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.addressData = res.data.data[0]
			        }
			    }
			   
			}); 
		},
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type){
			this.payType = type;
		},
		submit(){
			this.createOrder()
			// uni.redirectTo({
			// 	url: '/pages/money/pay'
			// })
		},
		initGoods()
		{
			var goodsMaps =[]
			for(var i=0;i<this.goodsData.length;i++)
			{
				var goods={}
				if(this.price>0)
				{
				goods={goodsId:this.goodsData[i].id,number:this.goodsData[i].number}
				}else
				{
				goods={goodsId:this.goodsData[i].id,number:1}
				}
				goodsMaps.push(goods)
			}
			
			return goodsMaps
		},
		createOrder(){
			var _this = this
			var goodsList = this.initGoods()
			//let myMap = new Map().set(this.goodsData.id,1);
			console.log(goodsList)	
			uni.request({
			    url:this.$url+'/order/add' ,//服务器端地址
			    data: {
					addressId: _this.addressData.id,
					goods:goodsList
					
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.payOrder(res.data.data)
			        }
			    }
			   
			});
			},
			
			payOrder(orderId){
				var _this = this
				console.log(orderId)
				uni.request({
				    url:this.$url+'/order/pay/'+orderId ,//服务器端地址
				    data: {
				    },
				    method: 'POST',
				    header: {
				        'content-type': 'application/json',
						'token':token,
				    },
				    success: (res) => { 
						console.log(res.data)
				        if (res.data.code == 200) {
				       uni.requestPayment({
				           timeStamp:res.data.data.timeStamp,
				           nonceStr:res.data.data.nonceStr,
				           package: res.data.data.packageValue,
				           signType:res.data.data.signType,
				           paySign:res.data.data.paySign,
				           success(res) {
							   uni.navigateTo({
							   	url: `/pages/money/paySuccess`
							   })
				           },
				           fail(err) {
							   
				           }
				       });
						}

				    }
				   
				});
				},
				 

	}
}
